/* src/components/Calculator.css */
.calculator {
    width: 300px;
    margin: auto;
    background-color: hwb(56 90% 4% / 0.823);
    border-radius: 10px;
    box-shadow: 0 4px 20px rgba(189, 84, 9, 0.897);
    overflow: hidden;
}

.display {
    background: rgba(230, 212, 9, 0.82);
    color: rgb(196, 50, 50);
    padding: 20px;
    text-align: right;
    font-size: 2em;
        font-weight: bold;
    
    
}

.result {
    color: #ce6011d8;
    font-size: 1.5em;
}

.buttons {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 10px;
    padding: 10px;
}

button {
    padding: 20px;
    font-size: 1.5em;
    background-color: #e3a71a;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.2s;
}

button:hover {
    background-color: #b38c00;
}

.clear {
    background-color: #dfb718;
}

.clear:hover {
    background-color: #c8c023;
}
